import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { TableData } from 'components/common';
import {
  filterCandidateForEnrollment,
} from 'services/Candidate';
import moment from 'moment';
import { numberToMoney } from 'utils/formatUtil';
import FormSearchStudent from './FormSearchStudent';
import FormInfoReceive from './FormInfoReceive';
import './style.scss';

const columns = [
  {
    title: 'Số báo danh',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Giới tính',
    dataIndex: 'genderText',
    key: 'genderText',
  },
  {
    title: 'Tổng điểm',
    dataIndex: 'totalGrade',
    key: 'totalGrade',
  },
  {
    title: 'Ngành',
    dataIndex: 'studyFieldName',
    key: 'studyFieldName',
  },
  {
    title: 'Ngày nhập học',
    dataIndex: 'enrolledDate',
    key: 'enrolledDate',
    render: date => <span>{ date ? moment(date).format("DD-MM-YYYY") : '' }</span>,
  },
  {
    title: 'Người tiếp nhận',
    dataIndex: 'enrolledCreator',
    key: 'enrolledCreator',
  },
  {
    title: 'Người thu tiền',
    dataIndex: 'feeRecipientName',
    key: 'feeRecipientName',
  },
  {
    title: 'Số tiền nộp',
    dataIndex: 'feeAmount',
    key: 'feeAmount',
    render: number => numberToMoney(number),
  },
];

class ReceiveAdmissionPapersPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    selectedStudent: null,
    certTypeId: null,
    trainTypeId: null,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onClickOnRowTable = (record) => {
    this.setState({
      selectedStudent: record,
    });
  }

  getRowClassCSS = (record) => {
    const { selectedStudent } = this.state;
    let nameClassCSS = 'row_style';
    if(selectedStudent && record.id === selectedStudent.id) {
      nameClassCSS = 'row_selected_style';
    }
    return nameClassCSS;
  }

  handleChangeQueryParams = () => {
    const tempQueryParams = getQueryParams();
    this.setState({ 
      paramSearch: tempQueryParams,
      certTypeId: tempQueryParams.certTypeId,
      trainTypeId: tempQueryParams.trainTypeId,
    });
  };

  reloadPage = () => {
    this.setState({ reload: this.state.reload + 1 });
  }

  render() {
    return (
      <Fragment>
        <Card title="Nhận giấy tờ nhập học" style={{display:'inherit'}}>
          <Row>
            <Col span={24}>
              <FormSearchStudent />
            </Col>
            <Col span={24}>
              <TableData
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                service={filterCandidateForEnrollment}
                columns={columns}
                rowKey="id"
                onRow={(record) => ({onClick: () => this.onClickOnRowTable(record)})}
                rowClassName={this.getRowClassCSS}
              />
            </Col>
          </Row>
        </Card>
        <Card title="Thông tin giấy tờ" style={{display:'inherit'}}>
          <Row>
            <Col span={24}>
              <FormInfoReceive 
                selectedStudent={this.state.selectedStudent} 
                reloadPage={this.reloadPage} 
                certTypeId={this.state.certTypeId}
                trainTypeId={this.state.trainTypeId}
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



