import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Form, Input, DatePicker, Checkbox, Select, message } from 'antd';
import { SelectAjax, TableData } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getStudyFields } from 'services/StudyField';
import { getPaperEnrollments } from 'services/PaperEnrollment';
import {
  receiveEnrollNote,
  exportEnrollNote,
  exportCandicateSummaryReport,
} from 'services/Candidate';
import { getClasss } from 'services/class';
import { BtnSave, BtnExport } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import moment from 'moment';

const paperColumns = [
  {
    title: 'Bắt buộc',
    dataIndex: 'isMandantory',
    key: 'isMandantory',
    render: isMandantory => (isMandantory ? 'Y' : 'N'),
  },
  {
    title: 'Tên giấy tờ',
    dataIndex: 'paperTypeName',
    key: 'paperTypeName',
  },
  {
    title: 'Số lượng',
    dataIndex: 'noOfPieces',
    key: 'noOfPieces',
  },
  {
    title: 'Số lượng thực tế',
    dataIndex: 'noOfPiecesReal',
    key: 'noOfPiecesReal',
    render: (noOfPiecesReal, record) => (
      <Input
        onChange={e => {
          record.noOfPiecesReal = e.target.value;
          return record;
        }}
      />
    ),
  },
];

const mapStateToProps = state => ({
  userId: state.app.userState.data
    ? state.app.userState.data.substring(
        state.app.userState.data.indexOf(`'`) + 1,
        state.app.userState.data.lastIndexOf(`'`)
      )
    : '',
});

@connect(mapStateToProps)
@Form.create({ name: 'FormInfoReceive' })
class FormInfoReceive extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
      clearData: 0,
      paramSearch: {},
      refTable: React.createRef(),
      loading: false,
    };
  }

  componentWillReceiveProps = nextProps => {
    if (nextProps.selectedStudent && nextProps.selectedStudent !== this.props.selectedStudent) {
      this.setState({
        paramSearch: {
          trainTypeId: nextProps.selectedStudent.trainTypeId,
          candidateId: nextProps.selectedStudent.id,
          yearEduId: getQueryParams().yearEduId,
        },
      });
      this.props.form.setFieldsValue({
        enrolledDate: nextProps.selectedStudent.enrolledDate
          ? moment(nextProps.selectedStudent.enrolledDate)
          : moment(),
        enrolledById: nextProps.selectedStudent.enrolledById || localStorage.getItem('app.Role'),
        studyFieldId: nextProps.selectedStudent.studyFieldId,
        classId: null,
      });
    }

    if (nextProps.trainTypeId && nextProps.trainTypeId !== this.props.trainTypeId) {
      this.props.form.setFieldsValue({
        trainTypeId: nextProps.trainTypeId,
      });
    }

    if (nextProps.certTypeId && nextProps.certTypeId !== this.props.certTypeId) {
      this.props.form.setFieldsValue({
        certTypeId: parseInt(nextProps.certTypeId),
      });
    }
  };

  onChangeTrainTypes = async value => {
    const params = Object.assign({}, this.state.paramSearch);
    params.trainTypeId = value;
    this.setState({
      paramSearch: params,
    });
  };

  reloadPage = () => {
    this.props.reloadPage();
    this.setState({ paramSearch: {}, clearData: this.state.clearData + 1 });
  };

  handleSave = e => {
    e.preventDefault();
    const { userId } = this.props;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState({ loading: true });
        if (values.enrolledDate) {
          values.enrolledDate = moment(values.enrolledDate).format('YYYY-MM-DD');
        }
        const dataOfTable = [...this.state.refTable.current.state.data];
        const paperList = [];
        for (let index = 0; index < dataOfTable.length; index += 1) {
          const element = Object.assign({}, dataOfTable[index]);
          let { noOfPiecesReal } = element;
          const { noOfPieces } = element;
          const { id } = element;
          noOfPiecesReal = !Number.isNaN(parseInt(noOfPiecesReal)) ? parseInt(noOfPiecesReal) : 0;
          paperList.push({ id, noOfPiecesReal, noOfPieces });
        }
        const requestParam = {
          ...values,
          classId: values.classId || null,
          paperList,
          candidateId: this.props.selectedStudent.id,
          code: this.props.selectedStudent.code,
          enrolledById: this.props.selectedStudent.enrolledById,
          userInputId: userId,
          // token:localStorage.getItem('app.token'),
        };
        receiveEnrollNote(requestParam)
          .then(() => {
            message.success('Nhận hồ sơ thành công');
            if (values.isExport) {
              exportCandicateSummaryReport(this.props.selectedStudent.id);
            }
            this.reloadPage();
            this.setState({ loading: false });
          })
          .catch(() => {
            message.error('Có lỗi xảy ra');
            this.setState({ loading: false });
          });
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { loading, paramSearch } = this.state;
    const {
      form: { getFieldDecorator },
    } = this.props;

    const { getFieldValue, setFieldsValue } = this.props.form;

    const studyFieldId = getFieldValue('studyFieldId');
    const trainTypeId = getFieldValue('trainTypeId');
    const certTypeId = getFieldValue('certTypeId');
    const { yearEduId } = paramSearch;

    return (
      <Form layout="vertical" onSubmit={this.handleSave}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Ngày nhập học">
              {getFieldDecorator('enrolledDate', {
                initialValue: moment(),
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Ngày nhập học!',
                  },
                ],
              })(<DatePicker style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Người tiếp nhận">
              {getFieldDecorator('enrolledById', {
                initialValue: localStorage.getItem('app.Role'),
              })(<Input disabled />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hệ NH">
              {getFieldDecorator('trainTypeId', {
                initialValue: trainTypeId || false,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Hệ NH!',
                  },
                ],
              })(<SelectAjax service={getTrainTypes} onChange={this.onChangeTrainTypes} />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Trình độ">
              {getFieldDecorator('certTypeId', {
                initialValue: certTypeId ? parseInt(certTypeId) : false,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Trình độ!',
                  },
                ],
              })(
                <Select>
                  <Select.Option value={2}>Trung học cơ sở</Select.Option>
                  <Select.Option value={1}>Trung học phổ thông</Select.Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ngành đào tạo">
              {getFieldDecorator('studyFieldId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Ngành đào tạo!',
                  },
                ],
              })(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Lớp">
              {getFieldDecorator('classId', { initialValue: false })(
                <SelectAjax
                  service={getClasss}
                  params={{ yearEduId, trainTypeId, studyFieldId }}
                  allowGetData={!!yearEduId && !!trainTypeId && !!studyFieldId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label=" ">
              {getFieldDecorator('isExport', {
                initialValue: false,
              })(<Checkbox>In phiếu khi lưu</Checkbox>)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={24}>
            <TableData
              clear={this.state.clearData}
              reload={this.state.reload}
              paramSearch={paramSearch}
              allowGetData={!!paramSearch.candidateId}
              service={getPaperEnrollments}
              columns={paperColumns}
              rowKey="id"
              ref={this.state.refTable}
            />
          </Col>
        </Row>
        <Row gutter={24}>
          <Col offset={8} span={12}>
            <BtnSave htmlType="submit" loading={loading} />
            <BtnExport onClick={() => exportEnrollNote(getQueryParams())} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoReceive;



