import React, { Component } from 'react';
import { Row, Col, Form, Input, DatePicker, Checkbox, Select } from 'antd';
import { isEmpty, find } from 'lodash';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getYearEducations } from 'services/yearEducationService';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import moment from 'moment';

const { RangePicker } = DatePicker;
@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const tempParams = getQueryParams();
    if (tempParams.certTypeId) {
      tempParams.certTypeId = parseInt(tempParams.certTypeId);
    }
    if (tempParams.fromDate) {
      tempParams.rangeDate = [];
      tempParams.rangeDate[0] = moment(tempParams.fromDate);
      tempParams.rangeDate[1] = moment(tempParams.toDate);
    }
    form.setFieldsValue(tempParams);
    this.setCurrentYear();
  };

  setCurrentYear = async () => {
    const { form } = this.props;
    const res = await getYearEducations();
    if (res.data && !isEmpty(res.data.items)) {
      const currentYear = find(res.data.items, item => item.fromYear === 2019);
      form.setFieldsValue({ yearEduId: currentYear ? currentYear.id : moment().year() });
    }
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (values.rangeDate && values.rangeDate.length) {
        [values.fromDate, values.toDate] = values.rangeDate;
        values.fromDate = moment(values.fromDate).format('YYYY-MM-DD');
        values.toDate = moment(values.toDate).format('YYYY-MM-DD');
        delete values.rangeDate;
      }
      delete values.rangeDate;
      console.log('Received values of form: ', values);
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Số BD">{getFieldDecorator('code', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Họ tên">{getFieldDecorator('fullname', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khối thi">{getFieldDecorator('testGroup', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngành thi">
              {getFieldDecorator('studyField', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Người tiếp nhận">
              {getFieldDecorator('enrolledRecipient', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Trình độ">
              {getFieldDecorator('certTypeId', { initialValue: 2 })(
                <Select>
                  <Select.Option value={2}>Trung học cơ sở</Select.Option>
                  <Select.Option value={1}>Trung học phổ thông</Select.Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Hệ NH">
              {getFieldDecorator('trainTypeId', {})(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm NH">
              {getFieldDecorator('yearEduId', {
                rules: [{ required: true, message: 'Vui lòng nhập thông tin!' }],
              })(<SelectAjax keyLabel="fromYear" service={getYearEducations} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngày nhập học">
              {getFieldDecorator('rangeDate', {})(<RangePicker />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ghi chú">{getFieldDecorator('note', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('withDocument', {
                valuePropName: 'checked',
                initialValue: false,
              })(<Checkbox>Đã nộp</Checkbox>)}
              {getFieldDecorator('withoutDocument', {
                valuePropName: 'checked',
                initialValue: false,
              })(<Checkbox>Chưa nộp</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={6} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



